import React from 'react';
import { useForm } from 'react-hook-form';
import classes from './Messenger.module.css';

const AddMessageForm = (props) => {

    const {
        register,
        handleSubmit,
        formState: { errors }
    } = useForm({ mode: 'onChange' });

    function onSubmit(data) {
        console.log(data)
        props.addMessageC({ id: Math.random() * 5, message: data.message, author: 'me' }, props.dialogId)
    }

    return (
        <div>
            <form onSubmit={handleSubmit(onSubmit)}>
                <div className={classes.textareaButton}>
                    <textarea
                        name='message'
                        ref={register({ required: 'пустое поле' })}
                        cols="70"
                        placeholder={errors.message ? 'error: пустое поле' : 'ваше сообщение'}
                        className={errors.message ? classes.redBorder : ''}
                    />
                    <button>Отправить</button>
                </div>
            </form>
        </div>

    )
};

export default AddMessageForm;