import React, { useEffect, useState } from 'react';
import Dialog from './Dialog';
import MessagesList from './MessagesList';
import classes from './Messenger.module.css'
import ava from '../../assets/avaImg/mal2.jpg';
import { NavLink, Route } from 'react-router-dom';
import { withRouter } from 'react-router';
import AddMessageForm from './AddForm';


const Messenger = (props) => {

    const [personDialog, setpersonDialog] = useState(null);

    const getPersonDialog = (m) => {
        setpersonDialog({ dialogname: m.dialogname, img: m.img })
    }

    let dialogId = props.match.params.dialogId;

    useEffect(() => {
        if (dialogId) {
            const m = [...props.messagesList].filter(el => el.id === dialogId)
            setpersonDialog({ dialogname: m[0].dialogname, img: m[0].img })
        } else {
            const m = [...props.messagesList].filter(el => el.id === dialogId)
            setpersonDialog({ dialogname: null, img: ava })
        }
    }, [])


    let dialog = props.messagesList;
    let messagesList = props.messagesList;

    let messagesData = messagesList.map((m, index) => <NavLink to={`/Messenger/` + m.id} key={m.id} onClick={() => getPersonDialog(m)} activeClassName={classes.active} ><MessagesList key={index} id={m.id} dialogList={m.dialogname} img={<img src={!m.img ? ava : m.img} />} /></NavLink>)


    return (
        <div className={classes.Dialogs}>
            <span style={{ display: "inline-block", backgroundColor: "white", boxShadow: "0 0 0 0.5px rgb(0,0,0, 0.5)", marginRight: "-5px" }}>
                {/* <span className={classes.showMessage} onClick={ShowMessage}>cкрыть сообщения</span> */}
                <div className={classes.messagelist}>
                    {messagesData}
                </div>
            </span>
            {/* } */}
            <div className={classes.dialog}>
                {
                    personDialog ?
                        <div className={classes.curentDialog}>
                            <div className={classes.name}>
                                <span style={{ opacity: '0.7', fontWeight: 'normal' }}>{personDialog.dialogname === null ? "выберите собеседника" : "ваша переписка с"} </span>{personDialog.dialogname}</div>
                            <img src={personDialog.img} alt="ava" />
                        </div> :
                        null
                }
                {
                    dialog.map((d, index) =>
                        <div key={d.id} className={classes.dialog2} >
                            <Route path={"/Messenger/" + d.id} render={() =>
                                <Dialog name={d.dialogname} img={d.img} key={index} index={index} id={d.id} messages={d.dialog} deleteMessage={props.deleteMessage} />
                            } />
                            {/* <AddMessageReduxForm dialogId={dialogId} onSubmit={addMessages} /> */}
                            {dialogId ?
                                <AddMessageForm addMessageC={props.addMessageC} dialogId={dialogId} /> :
                                ''
                            }
                        </div>)
                }
            </div>
        </div>
    )
};

export default withRouter(Messenger);