import { connect } from 'react-redux';
import { compose } from 'redux';
import { withAuthRedirect } from '../../Hoc/withAuthRedirect';
import { addMessageAC, deleteMessage, dialogType, messagesListType } from '../../redux/messagesReducer';
import { AppStateType, DispatchType } from '../../redux/redux-store';
import { profileType } from '../../types/types';
import Messenger from './Messenger';

type MapStatePropsType = {
    profile: profileType,
    newMLetter: string,
    dialog: Array<dialogType>,
    messagesList: Array<messagesListType>
}

type MapDispatchPropsType = {
    addMessageC: (messageBody: string, dialogId: string) => void
    deleteMessage: (index: number, id: number) => void
}


type PropsType = MapStatePropsType & MapDispatchPropsType

let MapStateToProps = (state: AppStateType): MapStatePropsType => {
    return {
        profile: state.ProfilePage.profile,
        newMLetter: state.MessagesPage.newMLetter,
        // dispatch: state.MessagesPage.dispatch,
        dialog: state.MessagesPage.dialog,
        messagesList: state.MessagesPage.messagesList
    }
};

let MapDispatchToProps = (dispatch: DispatchType): MapDispatchPropsType => {
    return {
        addMessageC(messageBody, dialogId) {
            dispatch(addMessageAC(messageBody, dialogId))
        },
        deleteMessage(index, id) {
            dispatch(deleteMessage(index, id))
        }
    }
}

export default compose(
    connect(MapStateToProps, MapDispatchToProps),
    withAuthRedirect
)(Messenger)

