import React from 'react';
import classes from './Messenger.module.css';
import udalit from '../../assets/avaImg/close.svg';
import ava from '../../assets/avaImg/mal2.jpg'

const Dialog = (props) => {

    const deleteMessage = (messagId) => {
        if (window.confirm('удалить сообщение?')) {
            props.deleteMessage(props.index, messagId)
        }
    }
    return (
        <div className={classes.itemD}>
            <div className={classes.dialogMessage}>
                {props.messages.map(message =>
                    <div key={message.id} className="dialog_container">
                        <span className={message.author === 'me' ? classes.mymessage : classes.span}>
                            <img src={message.author === props.name ? props.img : ava} alt="" /> {message.message}
                        </span>
                        <span className={classes.img} onClick={() => deleteMessage(message.id)}>
                            <img src={udalit} alt="delete" />
                        </span>
                    </div>)}
            </div>
        </div>
    )
};
;
export default Dialog;